import React, { Component } from "react";
import Tree from "react-d3-tree";
import data from "./khandelwal";

class MyTree extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount = () => {};

  renderTree() {
    return (
      <div
        id="treeWrapper"
        style={{
          marginTop: "50px",
          width: "100%",
          height: "100%",
          textAlign: "center"
        }}
      >
        <Tree orientation="horizontal" data={data.DATA_SET} />
      </div>
    );
  }

  render() {
    return <div style={{ height: "inherit" }}>{this.renderTree()}</div>;
  }
}
export default MyTree;
